import { FunctionComponent } from 'react';
import styled, { css } from 'styled-components';

import {
  CardHeader,
  CardContent,
  CardFooter,
  Link,
  Text,
  ForwardProps,
  StyledText,
  Actions,
  Flex,
  Tag,
  defaultThemeProp
} from '@pega/cosmos-react-core';
import { StyledCard } from '@pega/cosmos-react-core/lib/components/Card/Card';
import { StyledCardHeader } from '@pega/cosmos-react-core/lib/components/Card/CardHeader';
import { StyledCardContent } from '@pega/cosmos-react-core/lib/components/Card/CardContent';
import { StyledCardFooter } from '@pega/cosmos-react-core/lib/components/Card/CardFooter';
import { StyledBareButton } from '@pega/cosmos-react-core/lib/components/Button/BareButton';

import ArticleMeta from '../Article/ArticleMeta';

import { ArticleSummaryProps } from './ArticleList.types';

const ArticleCard = styled(StyledCard)(
  ({
    theme: {
      base: { spacing, palette }
    }
  }) => {
    return css`
      &:not(:last-child) {
        border-block-end: 0.0625rem solid ${palette['border-line']};
        border-radius: 0;
        padding-block-end: ${spacing};
      }
      &&& {
        border-radius: 0;
        > ${StyledCardHeader} {
          padding-block-start: 0;
          padding-inline: 0;
        }
        > ${StyledCardContent}, > ${StyledCardFooter} {
          padding-inline: 0;
        }
        > ${StyledCardContent} {
          padding-block-end: ${spacing};
        }
        > ${StyledCardContent} > ${StyledText} {
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
      }
    `;
  }
);

export const StyledQuickFilters = styled.div(({ theme }) => {
  const {
    base: { spacing }
  } = theme;

  return css`
    ${StyledBareButton} {
      margin-inline-start: 0;
      margin-block-start: ${spacing};
    }
  `;
});
StyledQuickFilters.defaultProps = defaultThemeProp;

const ArticleSummary: FunctionComponent<ArticleSummaryProps & ForwardProps> = (
  props: ArticleSummaryProps
) => {
  const {
    articleId,
    href,
    title,
    abstract,
    onTitleClick,
    primaryActions,
    meta,
    quickFilters,
    onQuickFilterClick,
    ...restProps
  } = props;

  return (
    <ArticleCard as='li' role='article' key={articleId} {...restProps}>
      <CardHeader container={{ alignItems: 'start', justify: 'between' }}>
        <Link href={href ?? '/'} onClick={onTitleClick}>
          {title}
        </Link>
        {primaryActions?.length && <Actions items={primaryActions} />}
      </CardHeader>
      <CardContent>
        <Text>{abstract}</Text>
      </CardContent>
      <CardFooter>
        {meta && <ArticleMeta {...meta} />}
        {quickFilters && quickFilters.length > 0 && (
          <Flex
            container={{ gap: 0.5, pad: [0.5, undefined], wrap: 'wrap' }}
            item={{
              grow: 1,
              basis: '100%'
            }}
            as={StyledQuickFilters}
          >
            {quickFilters.map(({ id, name }) => (
              <Tag
                onClick={
                  onQuickFilterClick ? e => onQuickFilterClick?.({ id, name }, e) : undefined
                }
              >{`#${name}`}</Tag>
            ))}
          </Flex>
        )}
      </CardFooter>
    </ArticleCard>
  );
};

export default ArticleSummary;
